import React, { Component } from 'react'
import axios from 'axios'
import './app.css'
import { dbURL } from '../config'

function RenderCard(props) {
    const { data } = props;
    return (
        <div className="card">
            <div className="card-image">

                <figure className="image is-4by3">
                    <img src={data.imgsrc} alt="Placeholder image" />
                </figure>

            </div>
            <div className="card-content">
                <p className="title is-4">{data.name}</p>
                <div className="content">
                    {data.desc}
                </div>
            </div>
            <footer className="card-footer">
                <a href="#" className="card-footer-item" onClick={() => props.removeCard(data._id)}>Delete</a>
            </footer>
        </div >
    )
}

export class ListAll extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: []
        };
        this.removeCard = this.removeCard.bind(this)
    }

    componentDidMount() {
        this.fetchData();
    }

    removeCard(id) {
        this.setState({ data: this.state.data.filter(item => item._id !== id) })
    }

    async fetchData() {
        axios({
            method: 'get',
            url: '/api/products'
        })
            .then(data => {
                this.setState({ data: data.data });
                console.log(data.data);
            })
            .catch(err => {
                console.log(err);
            })
    }

    render() {

        const Cards = this.state.data.map((item, index) => <RenderCard removeCard={this.removeCard} data={item} key={index.toString()} />)

        console.log("Cards", Cards)

        return (
            <>
                <section className="hero is-white">
                    <div className="hero-body">
                        <div className="container">
                            <h1 className="title">
                                All Products
                            </h1>
                        </div>
                    </div>
                </section>

                <section className="hero">
                    <div className="products">
                        {Cards.length>0? Cards: <div class="notification">No Data to show</div>}
                    </div>
                </section>
            </>
        )
    }
}

export default ListAll
